label {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  width: 80%;
  margin: 5px 5px 5px 5px;
}
label span {
  width: 120px;
}
label input,
textarea,
select {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 50%;
  margin: 5px 5px 5px 5px;
}
[type="submit"] {
  display: flex;
  justify-content: center;
  align-items: center;
  min-width: 100px;
  border-radius: 3px;
  border: 1px solid green;
  background: lightgreen;
  color: #404040;
  cursor: pointer;
}
[type="submit"]:hover {
  background: #65e765;
  border: 1px solid darkgreen;
  color: #d9d9d9;
}
.submitContainer {
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: space-around;
}
a {
  text-decoration: none;
  color: #e9fce9;
}
a:hover {
  color: #bdf5bd;
}
.signUpButton {
  margin: 5px 5px 5px 5px;
  width: 100px;
  height: 30px;
  border-radius: 3px;
  background: #bdf5bd;
  border: 1px solid #90ee90;
  color: grey;
}
.signUpButton:hover {
  background: #90ee90;
  color: #fff;
  border: 1px solid green;
}
.signUpButton a {
  color: #404040;
}
.signUpButton a:hover {
  color: #fff;
}
.pageTitle {
  display: flex;
  justify-content: center;
  align-items: flex-end;
}
.pageTitleHomePage {
  display: flex;
  justify-content: center;
  align-items: flex-end;
}
.emailInp {
  font-size: 18px;
}
.emailField {
  height: 26px;
}
.marginLogin {
  margin-top: 40px;
}
